* {
  font-family: Arial, Helvetica, sans-serif ;
}

body{
  background-color: #21232a;
  color: white;
  padding: 60px;
}

input:focus{
  outline: none;
}

input{
  font-size: 1.2rem;
  width: 100%;
  color: grey;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card{
  align-content: center;
  border: 1px solid grey;
  border-radius: 5px;
  margin: 10px;
  overflow: hidden;
}

.card p{
  padding: 5px;
  text-align: center;
}

.card img{
  max-height: 170px;
}